@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,800;1,800&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: #673F69;
    font-family: "Rubik", sans-serif;
    font-style: italic;
}

body{
    background-color: #ffae45b9;
}

.title {
    padding: 1rem;
    background-color: #FB6D48;
    margin: .5rem 4rem;
    border-radius: .3rem;
    text-align: center;
    box-shadow: 10px 10px 5px #673F69;
}

img {
    width: 300px;
    height: 300px;
    border-radius: .5rem;
}

main {
    margin-top: 3rem;
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    justify-content: center;
}

.card {
    border: 10px double #FB6D48;
    padding: .5rem; 
}

.names {
    text-align: center;
    border-top: 2px solid #FB6D48;
    margin-top: 1rem;
    font-weight: 800;
}

